.search {
}

.inputs {
    background-color: rgb(231, 231, 231);
    border-radius: 50px;
    width: 60vh;
    height:8vh;
    border: solid 1px rgba(128, 128, 128, 0);
    margin: 1vh 0;
    background-image: url(img/magnifying-glass-3-512.png);
    background-size: 4vh;
    background-repeat: no-repeat;
    background-position: center;
    // background-position-x: left;
    background-position-x: 3vh;
    padding-left: 10vh;
    &:focus {
        outline:none;
    }
}
.charList {
    border-top: solid 1px rgb(201, 201, 201);
    width: 640px;
    padding: 3vh 0 10vh;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    div {
        margin: 0 1vh;
        cursor: pointer;
        border-radius: 100%;
        border: solid 1px rgb(218, 218, 218);
        &:hover {
            opacity: .5;
        }
    }
    .apech {
        background-image: url(img/apechChar.png);
        width: 100px; height: 100px;
        background-size: cover;
        background-repeat: no-repeat;
    }
    .lion {
        background-image: url(img/lionChar.png);
        width: 100px; height: 100px;
        background-size:60%;
        background-repeat: no-repeat;
        background-position: center;
    }
}

.searchTop {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .searchGo {
        background: rgb(228, 228, 228);
        padding: 2vh;
        border-radius: 50px;
        color: rgb(129, 129, 129);
        font-size: 1.5vh;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 3vh;
        margin-bottom: 1vh;
        cursor: pointer;
        &:hover {
            box-shadow: 1px 1px 1px grey;
        }
    }
}



  @media all and (min-width:480px) and (max-width:639px){
    body {
        margin: 0 auto;
    }
    .charList {
        width: 100%;
    }
  }

  @media all and (max-width:479px) {
    // body { border: solid 2px pink; width: 100%;}
    .charList {
        width: 100%;
    }
    .inputs {
        width: 70%;
    }
  }
