

.gridBox {
    // border: solid 2px blue;
    width: 640px;
    text-align: left;
    height: 80vh;
    margin: 0 auto;
   .searchDetItem {
       width: 50%;
       display :inline-block;
       height: 50%;
       overflow: hidden;
       position: relative;
       p {
           margin: 0;
       }
    .itemImage {
        width: 100%; height: 70%;
        // border: solid 2px black;
        overflow: hidden;
        img {
            width: 100%;
            transition: .5s;
            &:hover {transform: scale(1.1)} 
        }
    }
    .itemInfo {
        // margin-top: 1.5vh;
        padding-left: 1vh;
        padding: 2vh;
    }
   }
}

.searchDetailBanner {
    // border: solid 2px blue;
    width: 640px;
    height: 20vh;
    margin: 1vh auto 1vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.apechBanner {
    background-image: url('./img/Category_Apeach_210303.jpg');
}
.lionBanner {
    background-image: url('./img/Category_Ryan_210303.jpg')
}

@media all and (min-width:480px) and (max-width:639px){
    body {
        height: 100%;
    }
    .searchDetailBanner {
        width: 100%;
    }
    .gridBox {
        width: 100%;
    }
  }

  @media all and (max-width:479px) {
    // body { border: solid 2px pink; width: 100%;}
    .searchDetailBanner {
        width: 100%;
    }
    .gridBox {
        width: 100%;
    }
    .gridBox .searchDetItem {
        height: 38%;
        .itemInfo {
            margin-top: 0;
            line-height: 2vh;
            font-size: 1.5vh;
        }
    }
  }
